body.login #login h1 a{background:url("../img/logos/client-logo.svg") no-repeat scroll center top transparent;background-size:cover;height:150px;width:300px}body.login{background:white}body.login #login{padding:10% 0 0}#wpadminbar{transition:all 0.4s ease}#wpadminbar.hide{transform:translate(0, -46px)}@media only screen and (min-width: 783px){#wpadminbar.hide{transform:translate(0, -32px)}}#admin-trigger{text-decoration:none;position:fixed;top:0;right:0;z-index:101;transition:all .4s ease}#admin-trigger:hover{opacity:1}#admin-trigger:after{background:#333;color:#fff;font-size:20px;display:block;line-height:100%;padding:5px 0 10px;width:35px;z-index:2000;transition:all .4s ease;content:"+";text-align:center}#admin-trigger.down:after{padding:6px 0 9px;transform:translate(0, 46px);content:"-"}@media only screen and (min-width: 783px){#admin-trigger.down:after{transform:translate(0, 32px)}}#format-tabs li:last-child{display:none}
